
document.addEventListener('DOMContentLoaded', function() {
    document.querySelector('.menu-burger').classList.add("hidden");
});



document.querySelector('.burger').addEventListener('click', function () {


    document.querySelector('.menu-burger').classList.toggle("hidden");


    setTimeout(() => {

    document.querySelector('.burger span').classList.toggle('active');
    document.querySelector('.menu-burger').classList.toggle("block");
    document.querySelector('.menu-burger').classList.toggle("animate");
    }, 300);


    setTimeout(() => {

        document.querySelector('.menu-burger').classList.toggle("hidden");


        }, 300);






})

